.message-list-container {
	margin-bottom: 16px;
	flex: 1;
	overflow: scroll;
}

.message-list {
	height: 100%;
	display: flex;
	flex-direction: column;
	align-items: flex-start;
}

.message {
	padding: 8px 16px;
	margin-bottom: 8px;
	background: var(--color-gray);
	border-radius: var(--border-radius);
	text-align: left;
}

.own-message {
	background: var(--color-blue);
	align-self: flex-end;
	text-align: right;
}

.sender {
	margin-bottom: 8px;
}
